.node-asu-event {
    margin-bottom: 20px;
    background-color: #fff;
    align-items: center;
    display: flex;
    flex-direction: column;

    @include respond-to-min(large) {
        flex-direction: row;
    }


    .node-type-asu-event &, .sidebar-view & {
        display: block;
    }

    @include respond-to-min(large) {
        .pane-asu-events-search-listing & {
            display: flex;
            align-items: center;
        }
    }


    .image-container {
        position: relative;
        height: 0;
        padding-bottom: 150px;
        overflow: hidden;
        margin-right: 30px;

        @include respond-to-min(large) {
            width: 360px;
            height: 200px;
        }


        img {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 100%;
            height: auto;
            transform: translate(-50%, -50%);

            .pane-asu-events-search-listing & {
                width: 260px;
                height: 146px;
                object-fit: cover;
            }
        }
    }

    .featured-right {
        color: #828282;
        font-size: 18px;

        h3 {
            margin-bottom: 0;

            a {
                color: #000;
                border-bottom-color: transparent;
            }
        }
    }
}